import BlogPreview from '@/components/BlogPreview.vue';
import db from '@/fb';
import {
  collection,
  query,
  getDocs,
} from 'firebase/firestore';

export default {
  data: () => ({
    totalBlogs: 0,
  }),
  components: {
    BlogPreview,
  },
  created() {
    getDocs(query(collection(db, 'blog'))).then((snapshot) => {
      this.totalBlogs = snapshot.size;
    });
  },
};
